@import url("https://cdn.rawgit.com/dreampulse/computer-modern-web-font/master/font/Serif/cmun-serif.css");
.help-container {
  overflow-x: hidden;
  max-width: 70ch; }

.help {
  width: 100%;
  padding: 16px 32px;
  background: #fff;
  font-family: "Computer Modern Serif", serif;
  font-size: 18px;
  -webkit-box-shadow: -2px 0 16px rgba(34, 34, 34, 0.1);
          box-shadow: -2px 0 16px rgba(34, 34, 34, 0.1);
  border-left: 1px solid #b1afa4;
  -webkit-animation: slide-in .2s ease-out;
          animation: slide-in .2s ease-out; }
  .help h1, .help h2, .help h3 {
    text-align: center; }
  .help table {
    max-width: 60ch; }
  .help td:nth-child(-n+2), .help em {
    text-align: right;
    font-style: normal;
    font-weight: bold; }
  .help td, .help th {
    padding: 4px 8px;
    vertical-align: top; }
  .help th:last-child {
    text-align: left; }
  .help .counter-arrow {
    display: inline-block;
    width: 0;
    height: 0;
    left: 0;
    border: 8px solid transparent;
    border-right: none;
    border-left-color: #fa8334;
    vertical-align: middle;
    margin: 0 4px; }
  .help .highlight-example {
    display: inline-block;
    height: 1.2em;
    width: 2em;
    border-radius: 3px;
    vertical-align: middle;
    margin: 0 4px; }

@-webkit-keyframes slide-in {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%); }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%); } }

@keyframes slide-in {
  0% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%); }
  100% {
    -webkit-transform: translateX(0%);
            transform: translateX(0%); } }



